:root {
  --color-background: #2b1b2d;
  --spacing-base: clamp(4px, 1vw, 16px);
  --image-size: clamp(300px, 40vw, 500px);
}

.root {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  background: linear-gradient(45deg, 
    #0a0014,
    #130029, 
    #1a003d, 
    #20004d, 
    #2a0066,
    #20004d,
    #1a003d
  );
  background-size: 400% 400%;
  animation: gradientBG 15s ease infinite;
}

@keyframes gradientBG {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

.container {
  width: 100%;
  max-width: 1200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
}

/* Контейнер для первого изображения */
.first-image-container {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

/* Контейнер для остальных изображений */
.other-images-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

/* Общие стили для карточек */
.game-card {
  width: var(--image-size);
  height: var(--image-size);
  position: relative;
  transition: all 0.3s ease;
}

/* Стили для изображений */
.game-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 0;
  transition: all 0.5s ease;
  animation: float 4s ease-in-out infinite;
  filter: drop-shadow(0 0 15px rgba(255, 255, 255, 0.3));
}

/* Анимация качания */
@keyframes float {
  0% { transform: translateY(0) translateX(0); }
  25% { transform: translateY(-10px) translateX(-5px); }
  50% { transform: translateY(0) translateX(0); }
  75% { transform: translateY(10px) translateX(5px); }
  100% { transform: translateY(0) translateX(0); }
}

/* Эффекты при наведении */
.game-card:hover {
  transform: none;
}

.game-card:hover img {
  filter: drop-shadow(0 0 25px rgba(255, 255, 255, 0.7));
}

/* Разные задержки анимации для разных карточек */
.game-card:nth-child(2) img {
  animation-delay: 0.2s;
}

.game-card:nth-child(3) img {
  animation-delay: 0.4s;
}

.game-card:nth-child(4) img {
  animation-delay: 0.6s;
}

/* Медиа-запрос для больших экранов */
@media (min-width: 1440px) {
  :root {
    --image-size: clamp(350px, 45vw, 550px);
  }
  
  .container {
    max-width: 1600px;
  }
}

/* Медиа-запрос для мобильных устройств */
@media (max-width: 768px) {
  :root {
    --image-size: clamp(250px, 80vw, 400px);
  }
  
  .container {
    gap: 20px;
  }
}





